.listTitle{
    height: 50px;
    width: 400px;
    margin: 50px 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: x-large;
}
.listEdit{
    height: 200px;
    width: 250px;
    margin-top: 125px;
    margin-left: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
@media screen and (max-width: 600px){
    .listEdit{
        margin-left: 0px;
    }
}
.topContainer{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
